			},
		},
		computed: {
			searchData() {
				let filData = this.dcdata.filter(item => {
					return item.name.includes(this.searchValue)
				})
				if(this.sortData) {
					filData.sort((a,b) => {
						return this.sortData == 1 ? a.price - b.price : b.price - a.price
					})
				}
